#section1 {
    height:100vh;
    width:100vw;
    background: url('../images/header_start_img.jpg');
    background-size:contain;
    background-repeat: no-repeat;
    color: rgb(82, 80, 80);
    font-weight: 300;
}

.nav{
    display:flex;
    justify-content: flex-end;
    margin-right: 10em;
   
}
.nav_buttons:hover{
    border-top: 1px solid black;
    border-left: 1px solid black;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
    cursor: pointer;
    color: black;
}

.nav_buttons {
    margin-top: 10px;
    padding: 10px;
    height:20px;
    border-top: 1px solid white;
    border-left: 1px solid white;
    border-right: 1px solid white;
    border-bottom: 1px solid white;
    
}

.start_button {
    @extend .nav_buttons;
    border-top: 1px solid rgb(66, 66, 66);
    border-left: 1px solid rgb(66, 66, 66);
    border-right: 1px solid rgb(167, 167, 167);
    border-bottom: 1px solid rgb(167, 167, 167);
    padding-right: 1.5em;
    padding-left: 1.5em;
}

.log_buttons {
    display: flex;
    justify-content: flex-end;
    margin-right: 10em;
}

.zaloz_konto{
    border-top: 1px solid yellow;
    border-left: 1px solid yellow;
    border-right: 1px solid yellow;
    border-bottom: 1px solid yellow;
}

.napis {
    display: flex;   
    font-size: 2em;
    justify-content: flex-end;
    font-family: 'Open Sans', sans-serif;
    margin-top: 5em;
    margin-right: 2em;
    text-align: center;
    line-height:1.3em;
}

.znaczek_container {
    display: flex;
    justify-content: flex-end;
    margin-top:4em;
    margin-bottom:1em;
    margin-right: 15em;
}
.znaczek{
    background: url("../images/decoration.png");
    height:20px;
    width: 200px;
    background-position: center;
    background-size: cover;
    
}

.opcje_container {
    display: flex;
    justify-content: flex-end;
    margin-right: 6em;
    margin-top: 3em;
}

.opcja {
    text-transform: uppercase;
    width:150px;
    padding: 1em;
    margin: 1em;
    border: 1px solid black;
    font-size: 1.5em;
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    flex-wrap: wrap;
    text-align: center;
    line-height:1.3em;
    border-top: 1px solid rgb(66, 66, 66);
    border-right: 1px solid rgb(66, 66, 66);
    border-left: 1px solid rgb(134, 134, 134);
    border-bottom: 1px solid rgb(134, 134, 134);
}

.opcja:hover{
    border-top: 1px solid black;
    border-right: 1px solid black;
    border-left: 1px solid black;
    border-bottom: 1px solid black;
    cursor: pointer;
    color:black;
}